body {
  font-family: Arial, sans-serif;
  margin: 0;
}

div.background {
  background-image: url('../images/black-background.jpg');
  min-height: 100dvh;
}

#logout {
  z-index: 2;
  position: absolute;
  top: 28px;
  right: 10px;
}


#container{
  margin-top: 20px;
}

#title{
  display: block;
  margin: auto;
  color: white;
  margin-bottom: 20px;
}


#first-col{
  height: 500;
  width: 460;
  background-color: var(--translucent-grey);
  margin: '2000px';
}

#second-col{
  height: 500;
  width: 400;
  background-color: var(--translucent-grey);
}

#third-col{
  height: 500;
  width: 460;
  background-color: var(--translucent-grey);
}

.new-img {
  margin-top: 10px;
  width:460px;
  max-height: 50dvh;
  flex-basis: 50%;
  border-radius: 40px;
}

input[type="file"] {
  display: none;
}

#choose-button button{
  color: red;
}




.custom-file-upload {
  display: block;
  width: 150px;
  text-align: center;
  margin: 50px auto ;
  color: white;
  font-size: 16px;
  background-color: #EA252E;
  border-radius: 20px;
  padding: 13px 2px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
  border: none;
}

.custom-file-upload:hover{
  background-color: white;
  color: red;
  border: 2px solid red;
}

.no-url-upload{
  margin-top: 10px;
  display: flex;
  font-size: 4.5rem;
  color: white;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #ec262a;
  width: 460px;
  height: 42vh;
  flex-basis: 50%;
  border-radius: 40px;
  cursor: pointer;
  border: 5px solid white;
}

.no-url-upload:hover{
  background-color: white;
  color: red;
  border: 5px solid red;
}

.update-delete-button-container {
  display: flex;
  flex-direction: column;
  gap: 2em;
  margin-top: 2em;
}

#final-button{
  display: block;
  margin: 75px auto;
  width: 300px;
  font-size: large;
  font-weight: 600;
}

#delete-button{
  background-color: #FBFF00;
  color: #FF0000;
  display: block;
  margin: -50px auto;
  width: 300px;
  font-size: large;
  font-weight: 600;
}

#delete-button:hover{
  background-color: #FF0000;
  color: #FBFF00;
  border-color: #FBFF00;
}


.error-msg{
  color: red;
  padding-top: 2px;
  margin-top: 5px;
  margin-bottom: -10px;
}

.link {
  text-decoration: none;
}




.delete-preview {
  position: fixed;
  z-index: 99;
  top: 200px;
  width: 500px;
  height: fit-content;
  padding-bottom: 25px;
  border-radius: 50px;
  background-color: var(--grey);
  text-align: center;
  color: var(--white);
}

.submit-button{
  margin:auto;
}

.cancel-button{
  background-color:rgb(51, 39, 39);
  
  margin-right: 10px;
}
.cancel-button:hover{
 
  margin-right: 10px;
}

.submit-button:hover {
  background-color: var(--white);
  color: var(--red);
  border: 2px solid var(--red);
}
